.header-area {
    &.sticky-header {
        position: fixed;
        z-index: 99999;
        left: 0;
        right: 0;
        top: 0;
        animation-name: fadeInDown;
        animation-duration: 1s;
        animation-fill-mode: both;

        @keyframes fadeInDown {
            0% {
                opacity: 0;
                transform: translate3d(0, -100%, 0);
            }

            100% {
                opacity: 1;
                transform: translateZ(0);
            }
        }
    }

    .container-fluid {
        padding-left: 50px;
        padding-right: 50px;

        .nav-bar {
            position: relative;
            display: flex;
            column-gap: 1.5rem;
            justify-content: space-between;
            align-items: center;
            margin-left: auto;
            margin-right: auto;

            .nav-logo {
                font-size: 24px;
                text-transform: uppercase;
                letter-spacing: 3px;
            }

            .main-menu {
                >ul {
                    display: flex;
                    gap: 30px;
                    list-style: none;
                    margin: 0;
                    padding: 0;
                    >li {
                        padding-top: 2rem;
                        padding-bottom: 2rem;
                        &.active{
                            a{
                                --tw-text-opacity: 1;
                                color: rgb(187 151 109 / var(--tw-text-opacity));
                            }
                        }
                        a {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            gap: 10px;
                            transition-property: all;
                            transition-timing-function: cubic-bezier(.4,0,.2,1);
                            transition-duration: .3s;
                        }
                    }
                }
            }
            .right-menu{
                width: 270px;
                display: flex;
                justify-content: flex-end;
            }
        }
    }
}

*,
:before,
:after {
    box-sizing: border-box;
    border-width: 0;
    border-style: solid;
    border-color: #e5e7eb;
}